import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

// import required modules
import { Pagination } from "swiper/modules";
import "./css/Partners.css"
const Partners = () => {
  return (
    <section id="partners" style={{ marginTop: "50px" }}>
      <h2>We're proud to work with our preferred partners</h2>
      <Swiper
        slidesPerView={2}
        loop={true}
        spaceBetween={30}
        // pagination={{
        //   clickable: false,
        // }}
        breakpoints={{
          440: {
            slidesPerView: 3,
            spaceBetween: 20,
          },
          640: {
            slidesPerView: 4,
            spaceBetween: 20,
          },
          768: {
            slidesPerView: 5,
            spaceBetween: 20,
          },
          1024: {
            slidesPerView: 6,
            spaceBetween: 20,
          },
          1200: {
            slidesPerView: 8,
            spaceBetween: 20,
          },
        }}
        modules={[Pagination]}
        className="mySwiper"
      >
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </SwiperSlide>
      </Swiper>
    </section>
  );
};

export default Partners;
